@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Source Code Pro', sans-serif;
}

select, input[type="text"]{
  width:100%;
  box-sizing:border-box;
}
.deployment-container {
    /* border: 1px solid lightgray; */
    /* padding-top: 10px; */
    /* padding-bottom: 10px; */
    width: 50%;
    text-align: left;
}

.deployment-type {
    width: 100%;
}

.dropdown-menu {
    border: 1px solid lightgray;
    border-radius: 2px;
    font-family: "Source Sans Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline: #00CBEC;
    padding: 5px;
}

.text-input {
    border: 1px solid lightgray;
    border-radius: 2px;
    font-family: "Source Code Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline: #00cbec;
    padding: 5px;
    margin-top: 3px;
}

.namespace-check {
    margin-top: 10px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: .8rem;
}

.namespace-label {
    margin-left: 4px;
}

.namespace-input {
    margin-top: 10px;
    width: 100%;
}
.actions-container {
    /* border: 1px solid lightgray; */
    padding-top: 10px;
    width: 50%;
    text-align: left;
}

.action-option {
    margin-top: 10px;
}
.command-container {
    margin-top: 40px;
}

.command-input-container {
    width: 100%;
}

.command {
    word-wrap: normal;
    width: 40%;
    max-height: 10%;
    margin: auto;
    background-color: #A112FF;
    color: white;
    font-family: "Source Code Pro", monospace;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px;
    white-space: normal;
    text-align: center;
    border: none;
    border-radius: 2px;
    font-weight: bold;
}

.command::-webkit-input-placeholder {
    color: white;
    font-weight: bold;
    background-color: #A112FF;
}

.command:-ms-input-placeholder {
    color: white;
    font-weight: bold;
    background-color: #A112FF;
}

.command::placeholder {
    color: white;
    font-weight: bold;
    background-color: #A112FF;
}

textarea.command[type="text"] {
    padding-top: 3%;
    padding-bottom: 3%;
    color: transparent;
    text-shadow: 0 0 0 #fff;
}

textarea.command[type="text"]:focus {
    outline:none;
}

button.copy-button:disabled,
button.copy-button[disabled] {
    border: 1px solid lightgray;
    border-radius: 3px;
    background-color: #ffffff;
    color: lightgray;
    width: 100px;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: regular;
    font-size: 1.0rem;
}

.btn-container {
    width: 40%;
    margin: auto;
    margin-top: 15px;
    text-align: right;
}

.copy-button {
    border: 2px solid #A112FF;
    border-radius: 3px;
    background-color: #ffffff;
    color: #A112FF;
    width: 100px;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 1.0rem;
}

.copy-button:hover {
    background-color: #A112FF;
    color: white;
    cursor: pointer;
}
.App {
  text-align: center;
  margin-top: 100px;
}

.container {
  width: 70%;
  margin: auto;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.actions {
  /* border: 1px solid #00cbec; */
  display: flex;
  flex-flow: column nowrap;
  width: 45%;
  min-width: 400px;
  min-height: 360px;
  margin-left: auto;
  margin-right: 0px;
}

.line-break {
  height: 275px;
  min-height: 275px;
  border-left: 1px solid lightgray;
}

.descriptions-container {
  width: 50%;
  min-height: 360px;
  min-width: 400px;
  /* border: 1px solid #00cbec; */
}

.logo {
  width: 300px;
}

.subtitle {
  font-family: "Source Code Pro", monospace;
  font-weight: 400;
  font-size: 1rem;
  color: #6C757D;
  padding-bottom: 3%;
}

.section {
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: 600;
  text-align: left;
}

.section.right { 
  /* border: 1px solid black; */
  padding-left: 44px;
}

.deploy-container {
  /* border: 1px solid #00cbec; */
  width: 65%;
  margin-left: auto;
}

.desc-list {
    list-style: none;
    text-align: left;
    font-family: "Source Code Pro", monospace;
    font-size: .9rem;
}

.description {
    list-style: none;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 5px;
}

.row {
    padding-top: 2px;
    padding-bottom: 2px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    font-size: .9rem;
    cursor: default;
    border-radius: 2px;
    padding-left: 5px;
    padding-right: 5px;
    max-width: 340px;
}

.row:hover {
    background-color: rgb(248, 248, 248);
}

.row.expanded {
    background-color: rgb(248,248,248);
    font-weight: bold;
}

.expand {
    border: none;
    background-color: transparent;
    font-family: "Source Code Pro", monospace;
    font-size: .9rem;
    cursor: pointer;
    transition: .2s;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.info-key {
    font-weight: bold;
}

.rotated {
    transform: rotate(-90deg);
}

.command-info {
    background-color: rgb(248,248,248);
    font-family: "Source Code Pro", Arial, Helvetica, sans-serif;
    font-size: .9rem;
    padding-bottom: 10px;
    width: 350px;
}
